[class*="__container"]{max-width:1200px;margin:0 auto}@media (max-width: 1220px){[class*="__container"]{max-width:1000px}}@media (max-width: 1000px){[class*="__container"]{max-width:800px}}@media (max-width: 830px){[class*="__container"]{max-width:95%}}@media (max-width: 768px){.button-secondary{display:block;margin-bottom:0.5rem;width:150px}}.button-secondary{background-color:#fff;color:#0071E3;font-size:1rem;line-height:1rem;padding:12px 18px;border:1px solid #0071E3;border-radius:0.5rem;cursor:pointer;transition:all ease-in-out .2s}.button-secondary:hover{-webkit-box-shadow:8px 8px 7px 0px rgba(0,0,0,0.2);-moz-box-shadow:8px 8px 7px 0px rgba(0,0,0,0.2);box-shadow:8px 8px 7px 0px rgba(0,0,0,0.2)}@media (max-width: 900px){.button-secondary{padding:10px 12px;font-size:0.8rem}}
