[class*="__container"]{max-width:1200px;margin:0 auto}@media (max-width: 1220px){[class*="__container"]{max-width:1000px}}@media (max-width: 1000px){[class*="__container"]{max-width:800px}}@media (max-width: 830px){[class*="__container"]{max-width:95%}}.header{position:fixed;top:0;left:0;width:100%;z-index:12;padding:12px 20px;border-bottom:1px solid #DBDBDB;background-color:#fff}.header .navbar{color:rgba(0,0,0,0.64);z-index:12;max-width:1400px;margin:0 auto}.header .navbar input[type="checkbox"],.header .navbar .hamburger-lines{display:none}.header .navbar__wrapper{display:flex;justify-content:space-between;align-items:center;gap:40px}@media (max-width: 1020px){.header .navbar__wrapper{gap:20px}}.header .navbar__wrapper .menu__items{width:100%;order:2;display:flex;justify-content:space-between;flex-wrap:wrap}.header .navbar__wrapper .menu__items-data{display:flex;gap:40px}@media (max-width: 1020px){.header .navbar__wrapper .menu__items-data{gap:20px}}.header .navbar__wrapper .menu__items .menu__item{position:relative;font-size:1rem;line-height:16px;display:flex;align-items:center;transition:width 0.3s ease 0s, left 0.3s ease 0s}@media (max-width: 900px){.header .navbar__wrapper .menu__items .menu__item{font-size:0.8rem}}.header .navbar__wrapper .menu__items .menu__item a::after{content:"";position:absolute;bottom:0;left:50%;display:block;background:none repeat scroll 0 0 transparent;height:3px;width:0;background:#0A84FF;transition:width 0.3s ease 0s, left 0.3s ease 0s}.header .navbar__wrapper .menu__items .menu__item a:hover{color:#000}.header .navbar__wrapper .menu__items .menu__item a:hover::after{width:100%;left:0}@media (max-width: 768px){.header .navbar .navbar__wrapper input[type="checkbox"],.header .navbar .navbar__wrapper .hamburger-lines{display:block}.header .navbar .navbar__wrapper{display:block;position:relative;height:64px}.header .navbar .navbar__wrapper input[type="checkbox"]{position:absolute;display:block;height:32px;width:30px;top:20px;left:20px;z-index:5;opacity:0}.header .navbar .navbar__wrapper input[type="checkbox"]:checked ~ .menu__items{transform:translateX(0)}.header .navbar .navbar__wrapper input[type="checkbox"]:checked ~ .hamburger-lines .line1{transform:rotate(35deg)}.header .navbar .navbar__wrapper input[type="checkbox"]:checked ~ .hamburger-lines .line2{transform:scaleY(0)}.header .navbar .navbar__wrapper input[type="checkbox"]:checked ~ .hamburger-lines .line3{transform:rotate(-35deg)}.header .navbar .navbar__wrapper .hamburger-lines{display:block;height:23px;width:35px;position:absolute;top:17px;left:20px;z-index:2;display:flex;flex-direction:column;justify-content:space-between}.header .navbar .navbar__wrapper .hamburger-lines .line{display:block;height:4px;width:100%;border-radius:10px;background:#000}.header .navbar .navbar__wrapper .hamburger-lines .line1{transform-origin:0% 0%;transition:transform 0.4s ease-in-out}.header .navbar .navbar__wrapper .hamburger-lines .line2{transition:transform 0.2s ease-in-out}.header .navbar .navbar__wrapper .hamburger-lines .line3{transform-origin:0% 100%;transition:transform 0.4s ease-in-out}.header .navbar .menu__items{padding-top:150px;background-color:#fff;height:100vh;width:100vw;transform:translate(-150%);display:block;margin-left:-30px;padding-left:50px;transition:transform 0.5s ease-in-out;box-shadow:5px 0px 10px 0px #aaa}.header .navbar .menu__items-data{display:block;margin-bottom:1.5rem;max-width:fit-content}.header .navbar .menu__items-data .menu__item{margin-bottom:1.5rem}.header .navbar .menu__items-data .menu__item-avatar{display:none}.header .navbar .menu__items li{margin-bottom:1.5rem;font-size:1.3rem;font-weight:500}.header .navbar .logo{position:absolute;top:5px;right:15px}}
