[class*="__container"] {
  max-width: 1200px;
  margin: 0 auto;
  @media (max-width: 1220px) {
    max-width: 1000px;
  }
  @media (max-width: 1000px) {
    max-width: 800px;
  }
  @media (max-width: 830px) {
    max-width: 95%;
  }
}

%buttonMobile {
  @media (max-width: 768px) {
    display: block;
    margin-bottom: 0.5rem;
    width: 150px;
  }
}