[class*="__container"]{max-width:1200px;margin:0 auto}@media (max-width: 1220px){[class*="__container"]{max-width:1000px}}@media (max-width: 1000px){[class*="__container"]{max-width:800px}}@media (max-width: 830px){[class*="__container"]{max-width:95%}}.tasks{padding-bottom:7.5rem}@media (max-width: 768px){.tasks{padding-bottom:3.5rem}}.tasks h2{font-family:'GraphikLCG-Bold';font-weight:700;font-size:2.6rem;line-height:3.25rem;color:#242424;max-width:80%;margin-bottom:40px}.tasks__tabs{margin-bottom:7.5rem}@media (max-width: 768px){.tasks__tabs{margin-bottom:3.5rem}}.tasks__data{display:grid;grid-template-columns:repeat(auto-fit, minmax(255px, 1fr));gap:3.75rem}
