[class*="__container"]{max-width:1200px;margin:0 auto}@media (max-width: 1220px){[class*="__container"]{max-width:1000px}}@media (max-width: 1000px){[class*="__container"]{max-width:800px}}@media (max-width: 830px){[class*="__container"]{max-width:95%}}.options{margin-bottom:7.5rem;background-color:#F7F7F5}@media (max-width: 768px){.options{margin-bottom:3.5rem}}.options__content{display:flex;justify-content:space-between;align-items:center;gap:3.75rem}@media (max-width: 768px){.options__content{flex-direction:column-reverse;padding-bottom:30px}}.options__content .content__text{flex:1 1 50%}.options__content .content__text h2{font-family:'GraphikLCG-Bold';font-weight:700;font-size:2.6rem;line-height:3.25rem;color:#242424;margin-bottom:1.5rem}.options__content .content__text p{font-weight:400;font-size:1.5rem;line-height:2.25rem;color:rgba(0,0,0,0.64)}.options__content .content__pic{flex:1 1 50%}.options__trackers{display:grid;grid-template-columns:repeat(auto-fit, minmax(360px, 1fr));gap:3.37rem;margin-bottom:7.5rem}@media (max-width: 768px){.options__trackers{margin-bottom:3.5rem}}@media (max-width: 370px){.options__trackers .tracker{width:90%}}.options__trackers .tracker img{margin-bottom:2.25rem}.options__trackers .tracker h3{font-family:'GraphikLCG-Bold';font-weight:700;font-size:1.5rem;line-height:2rem;color:#242424;margin-bottom:0.5rem}.options__trackers .tracker p{font-weight:400;font-size:1.1rem;line-height:1.75rem;color:rgba(0,0,0,0.64)}
